<template>
    <div class="dingshi">
        <div class="tabletit">定时生成、发送绩效考核报表</div>
        <div>
            <el-select style="margin-left:.6rem;" v-model="value" placeholder="请选择">
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>
            <el-button type="primary">添加</el-button>
        </div>
        <el-table :data="tableData.slice((currpage - 1) * pagesize, currpage * pagesize)" style="width: 100%">
            <el-table-column fixed prop="date" label="姓名" width="150"></el-table-column>
            <el-table-column prop="name" label="手机号"></el-table-column>
            <el-table-column prop="address" label="部门"></el-table-column>
            <el-table-column prop="ress" label="职务"></el-table-column>
            <el-table-column prop="fenshu" label="分数"></el-table-column>
        </el-table>
        <el-pagination background 
            layout="prev, pager, next, sizes, total"
            :page-sizes="[5, 10]"
            :page-size="pagesize"
            :total="tableData.length"
            @current-change="handleCurrentChange" 
            @size-change="handleSizeChange"
            style="margin-top:10px; margin-bottom:10px;">
        </el-pagination>
    </div>
</template>

<script>
export default {
    name:"dingshi",
    data(){
        return{
            currentPage: 4,
            pagesize:10,
            currpage:1,
            tableData: [{
                date: '吴庆',
                name: '无',
                address: '办公室',
                ress:"教师",
                fenshu:""
            }, {
                date: '陈大锋',
                name: '无',
                address: '教务处',
                ress:"学分制管理员/大专部主任",
                fenshu:""
            }, {
                date: '崔老师',
                name: '114',
                address: '校领导',
                ress:"副校长",
                fenshu:"44"
            }, {
                date: '李鸿禧',
                name: '无',
                address: '军教团委',
                ress:"教师",
                fenshu:""
            }, {
                date: '马喆',
                name: '无',
                address: '军教团委',
                ress:"教师",
                fenshu:""
            }, {
                date: '杨建松',
                name: '无',
                address: '就业处',
                ress:"教师",
                fenshu:""
            }, {
                date: '郝老师',
                name: '无',
                address: '总务处',
                ress:"总务处主任",
                fenshu:"90"
            }],
            options: [{
            value: '选项1',
            label: '魏老师'
            }, {
            value: '选项2',
            label: '黄江元'
            }, {
            value: '选项3',
            label: '韩兴华'
            }, {
            value: '选项4',
            label: '李文志'
            }, {
            value: '选项5',
            label: '余君'
            }],
            value: ''
        }
    },
    methods: {
      handleSizeChange(val) {
        this.pagesize = val;
      },
      handleCurrentChange(val) {
        this.currpage = val;
      }
    },
}
</script>

<style scoped>
.dingshi{
    width: 90%;
    height: 100%;
    margin: auto;
    margin-top: .5rem;
}
.tabletit{
    font-size: .5rem;
    text-align: center;
    margin-bottom: .3rem;
}
</style>


