<template>
  <div class="hello">
    <div class="tabs" v-for="(tab,index) in tabs" :key="index">
      <span><router-link :to="tab.url">{{tab.name}}</router-link></span>
    </div>
    <!-- <div class="">
      诊改标准体系
    </div>
    <div class="">
      诊改绩效体系
    </div> -->
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      tabs:[
        {
          name:"诊改目标体系",
          url:"mubiao"
        },
        {
          name:"诊改标准体系",
          url:"biaozhun"
        },
        {
          name:"诊改绩效体系",
          url:"jixiao"
        },
      ]
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
  margin: 0;
  padding: 0;
  list-style: none;
}
.hello{
  width: 100%;
  height: 100%;
}
.tabs{
  width: 45%;
  height: 4.5rem;
  float: left;
  margin-left: .3rem;
  margin-top: 1rem;
  border-radius: .4rem;
  line-height: 4.5rem;
  text-align: center;
}
.tabs:nth-child(1){
  background-color: #768395;
}
.tabs:nth-child(2){
  background-color: #7CB349;
}
.tabs:nth-child(3){
  background-color: #358FE2;
}
.tabs span{
  font-size: .45rem;
}
a{
  text-decoration: none;
  color: aliceblue;
}
</style>



