import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import mubiao from '@/components/mubiao'
import biaozhun from '@/components/biaozhun'
import jixiao from '@/components/jixiao'
import sannian from '@/components/sannian'
// import xiangmu from '@/components/xiangmu'
// import renwu from '@/components/renwu'
// import danxiang from '@/components/danxiang'
import wode from '@/components/wode'
import xiashu from '@/components/xiashu'
import paihang from '@/components/paihang'
import tisheng from '@/components/tisheng'
import quanxiao from '@/components/quanxiao'
import dingshi from '@/components/dingshi'
import xiangqing from '@/components/xiangqing'
import tree from '@/components/tree'
import add from '@/components/add'
import bianji from '@/components/bianji'
import fenjie from '@/components/fenjie'
import login from '@/components/login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/mubiao',
      name: 'mubiao',
      component: mubiao
    },
    {
      path: '/biaozhun',
      name: 'biaozhun',
      component: biaozhun
    },
    {
      path: '/jixiao',
      name: 'jixiao',
      component: jixiao
    },
    {
      path: '/sannian',
      name: 'sannian',
      component: sannian
    },
    // {
    //   path: '/xiangmu',
    //   name: 'xiangmu',
    //   component: xiangmu
    // },
    // {
    //   path: '/renwu',
    //   name: 'renwu',
    //   component: renwu
    // },
    // {
    //   path: '/danxiang',
    //   name: 'danxiang',
    //   component: danxiang
    // },
    {
      path: '/wode',
      name: 'wode',
      component: wode
    },
    {
      path: '/xiashu',
      name: 'xiashu',
      component: xiashu
    },
    {
      path: '/paihang',
      name: 'paihang',
      component: paihang
    },
    {
      path: '/tisheng',
      name: 'tisheng',
      component: tisheng
    },
    {
      path: '/quanxiao',
      name: 'quanxiao',
      component: quanxiao
    },
    {
      path: '/dingshi',
      name: 'dingshi',
      component: dingshi
    },
    {
      path: '/xiangqing',
      name: 'xiangqing',
      component: xiangqing
    },
    {
      path: '/tree',
      name: 'tree',
      component: tree
    },
    {
      path: '/add',
      name: 'add',
      component: add
    },
    {
      path: '/bianji',
      name: 'bianji',
      component: bianji
    },
    {
      path: '/fenjie',
      name: 'fenjie',
      component: fenjie
    },
  ]
})



