<template>
    <div>
        <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
    </div>
</template>
<script>

export default {
    name:"tree",
    data(){
        return{
            data: [{
                label: '设施建设目标（倪老师）',
                children: [{
                    label: '实训车间建设（崔老师）',
                    children: [{
                    label: '改善教师办公环境（崔老师）'
                    }]
                }]
                }, {
                label: '学生宿舍改造目标（崔老师）',
                children: [{
                    label: '14465（崔老师）',
                    children: [{
                    label: '新建立一个新能源实训车间'
                    }]
                }, {
                    label: '大师傅但是（倪老师）',
                    children: [{
                    label: '新能源实训车间的增加（郭老师）'
                    }]
                }]
                }, {
                label: '教学科研目标（倪老师）',
                children: [{
                    label: '制定教学科研相关工作（崔老师）',
                    children: [{
                    label: '完成一个市级以上重点课题申报（崔老师）'
                    }]
                }, {
                    label: '打击校园暴力（倪老师）',
                    children: [{
                    label: '减少校园斗殴事件（倪老师）'
                    }]
                }]
                }],
                defaultProps: {
                children: 'children',
                label: 'label'
                }
        }
    },
    methods: {
      handleNodeClick(data) {
        console.log(data);
      }
    },
    mounted(){
        
    }
}
</script>
<style scoped>

</style>



