<template>
    <div class="mubiao">
        <div class="tabs" v-for="(tab,index) in tabs" :key="index">
            <span><router-link :to="tab.url">{{tab.name}}</router-link></span>
        </div>
    </div>
</template>

<script>
export default {
    name:"jixiao",
    data(){
        return{
            tabs:[
                {
                    name:"我的绩效分数",
                    url:"wode"
                },
                {
                    name:"下属绩效分数表",
                    url:"xiashu"
                },
                {
                    name:"本部门绩效排行表",
                    url:"paihang"
                },
                {
                    name:"本部门本月绩效提升排行表",
                    url:"tisheng"
                },
                {
                    name:"全校绩效排行表",
                    url:"quanxiao"
                },
                {
                    name:"定时生成、发送绩效考核报表",
                    url:"dingshi"
                }
            ]
        }
    }
}
</script>

<style scoped>
.mubiao{
    width: 100%;
    height: 100%;
}
.tabs{
  width: 45%;
  height: 4.5rem;
  float: left;
  margin-left: .3rem;
  margin-top: 1rem;
  border-radius: .4rem;
  line-height: 4.5rem;
  text-align: center;
  color: aliceblue;
}
.tabs:nth-child(1){
  background-color: #768395;
}
.tabs:nth-child(2){
  background-color: #7CB349;
}
.tabs:nth-child(3){
  background-color: #358FE2;
}
.tabs:nth-child(4){
  background-color: #505050;
}
.tabs:nth-child(5){
  background-color: #E42739;
}
.tabs:nth-child(6){
  background-color: #809295;
}
.tabs span{
  font-size: .4rem;
}
a{
  text-decoration: none;
  color: aliceblue;
}
</style>


