<template>
    <div class="mubiao">
        <div class="tabs" @click="getCity(tab)" v-for="(tab,index) in tabs" :key="index">
            <span>{{tab.name}}</span>
        </div>
    </div>
</template>

<script>
export default {
    name:"mubiao",
    data(){
        return{
            tabs:[
                {
                    name:"三年规划/年度计划",
                    url:"sannian",
                    dj:1
                },
                {
                    name:"项目规划",
                    url:"sannian",
                    dj:2
                },
                {
                    name:"任务清单",
                    url:"sannian",
                    dj:3
                },
                {
                    name:"单项任务",
                    url:"sannian",
                    dj:4
                }
            ]
        }
    },
    methods:{
      getCity(tab){
        this.$router.push({
          name: 'sannian',
          query: {
            dj: tab.dj
          }
        })
      }
    }
}
</script>

<style scoped>
.mubiao{
    width: 100%;
    height: 100%;
}
.tabs{
  width: 45%;
  height: 4.5rem;
  float: left;
  margin-left: .3rem;
  margin-top: 1rem;
  border-radius: .4rem;
  line-height: 4.5rem;
  text-align: center;
}
.tabs:nth-child(1){
  background-color: #768395;
}
.tabs:nth-child(2){
  background-color: #7CB349;
}
.tabs:nth-child(3){
  background-color: #358FE2;
}
.tabs:nth-child(4){
  background-color: #505050;
}
.tabs span{
  font-size: .45rem;
  color: aliceblue;
}
a{
  text-decoration: none;
  color: aliceblue;
}
</style>


