<template>
    <div class="quanxiao">
        <div class="tabletit">全校绩效排行表</div>
        <el-table :data="tableData.slice((currpage - 1) * pagesize, currpage * pagesize)" style="width: 100%">
            <el-table-column fixed prop="date" label="姓名" width="150"></el-table-column>
            <el-table-column prop="name" label="手机号"></el-table-column>
            <el-table-column prop="address" label="部门"></el-table-column>
            <el-table-column prop="ress" label="职务"></el-table-column>
            <el-table-column prop="fenshu" label="分数"></el-table-column>
        </el-table>
        <el-pagination background 
            layout="prev, pager, next, sizes, total"
            :page-sizes="[5, 10]"
            :page-size="pagesize"
            :total="tableData.length"
            @current-change="handleCurrentChange" 
            @size-change="handleSizeChange"
            style="margin-top:10px; margin-bottom:10px;">
        </el-pagination>
    </div>
</template>

<script>
export default {
    name:"quanxiao",
    data(){
        return{
            currentPage: 4,
            pagesize:10,
            currpage:1,
            tableData: [{
                date: '郝老师',
                name: '无',
                address: '总务处',
                ress:"总务处主任",
                fenshu:"90"
            }, {
                date: '郭老师',
                name: '无',
                address: '总务处',
                ress:"教师",
                fenshu:"80"
            }, {
                date: '倪老师',
                name: '158694523658',
                address: '校长',
                ress:"校长",
                fenshu:"28"
            }, {
                date: '崔老师',
                name: '114',
                address: '校领导',
                ress:"副校长",
                fenshu:"44"
            }, {
                date: '李永康',
                name: '无',
                address: '计算机专业部',
                ress:"教师",
                fenshu:""
            }, {
                date: '陈起星',
                name: '无',
                address: '汽车专业部',
                ress:"教师",
                fenshu:""
            }, {
                date: '陈彬',
                name: '无',
                address: '汽车专业部',
                ress:"教师",
                fenshu:""
            }]
        }
    },
    methods: {
      handleSizeChange(val) {
        this.pagesize = val;
      },
      handleCurrentChange(val) {
        this.currpage = val;
      }
    },
}
</script>

<style scoped>
.quanxiao{
    width: 90%;
    height: 100%;
    margin: auto;
    margin-top: .5rem;
}
.tabletit{
    font-size: .5rem;
    text-align: center;
    margin-bottom: .3rem;
}
</style>


