<template>
    <div class="niandu">
        <div class="ji" v-for="(tab,index) in tabs" :key="index" @click="jiao(tab)">
            <div class="top">
                <div class="left">
                    <ul>
                        <li v-if="tab.importancedj == 1" class="tab">急</li>
                        <li v-else-if="tab.importancedj == 2" class="tab" style="background-color : blue">普</li>
                        <li v-else-if="tab.importancedj == 3" class="tab" style="background-color : #6DBF67">缓</li>
                        <li>{{tab.funds}}</li>
                        <li>万元</li>
                        <li v-if="xing"><i @click="gz" class="el-icon-star-off"></i></li>
                        <li v-else><i @click="qgz" style="color:red;" class="el-icon-star-on"></i></li>
                    </ul>
                </div>
                <div class="right">
                    <ul>
                        <li>{{tab.target_name}}</li>
                        <li>{{tab.target_info}}</li>
                    </ul>
                </div>
            </div>
            <div class="bottom">
                <ul>
                    <li><el-progress :text-inside="true" :stroke-width="18" :percentage="tab.progress"></el-progress></li>
                    <li>{{tab.start_time}}~{{tab.end_time}}</li>
                    <li v-if="tab.userEntity[0]">{{tab.userEntity[0].name}}</li>
                    <!-- <li v-if="tab.userEntity[1]">{{tab.userEntity[1].name}}</li>
                    <li v-if="tab.userEntity[2]">{{tab.userEntity[2].name}}</li> -->
                </ul>
            </div>
        </div>
        <div class="xin">
            <router-link to="/add">
                <p><i class="el-icon-circle-plus"></i></p>
            </router-link>
        </div>
    </div>
</template>

<script>

import axios from 'axios';

export default {
    name:"sannian",
    data(){
        return{
            xing:true,
            tabs:[]
        }
    },
    methods:{
        gz(e){
            this.xing = false;
            this.$message.success('关注成功');
        },
        qgz(e){
            this.xing = true;
            this.$message.warning('您已取消关注');
        },
        jiao(tab){
            console.log(tab)
            console.log(tab.id)
            this.$router.push({
                name: 'xiangqing',
                query: {
                    targetid:tab.id
                }
            })
        }
    },
    mounted(){
        var dj = this.$route.query.dj
        var that = this;
        console.log(dj)
        $.ajax({
            type: "post",
            url: this.baseUrl+"/ydjxzg/mb/wxndgh.action",
            data: {
                userid:1,
                dj:dj
            },
            dataType: "json",
            success: function (res) {
                console.log(res)
                that.tabs = res.list[0]
            }
        });
    }
}
</script>

<style scoped>
.ji{
    float: left;
    width: 90%;
    height: 100%;
    margin: auto;
    box-shadow: .05rem .05rem .05rem .05rem #cccccc;
    margin: .5rem 0 0 .5rem;
}
.left{
    position: absolute;
    margin-left: .3rem;
    text-align: center;
}
.right{
    margin: 0 .5rem 0 1.5rem;
}
.left ul li:nth-child(1){
    font-size: .5rem;
    background-color: red;
    padding: .2rem;
    color: aliceblue;
    border-radius: 0.2rem;
    margin-top: .5rem;
}
.left ul li:nth-child(2){
    font-size: .4rem;
    margin-top: .2rem;
    color: red;
}
.left ul li:nth-child(3){
    font-size: .4rem;
    margin-top: .02rem;
    color: red;
}
.left ul li:nth-child(4),.left ul li:nth-child(5){
    font-size: .3rem;
    border: 0.01rem solid #cccccc;
    margin-top: .2rem;
    padding: .1rem;
}
.right ul li:nth-child(1){
    font-size: .5rem;
    padding-top:.5rem; 
}
.right ul li:nth-child(2){
    font-size: .3rem;
    padding-top: .5rem;
    line-height: .5rem;
}
.bottom{
    margin-top: .6rem;
}
.bottom ul li:nth-child(1){ 
    float: left;
    width: 90%;
    margin-left: .3rem;
    margin-top: 1.2rem;
}
.bottom ul li:nth-child(2){
    float: left;
    width: 45%;
    height: .8rem;
    line-height: .8rem;
    text-align: center;
    margin: .5rem 0 0 .2rem;
    background-color: #FDD6D9;
    padding: .05rem;
    border-radius: .2rem;
    color: red;
}
.bottom ul li:nth-child(3){
    float: right;
    margin: .8rem .5rem .8rem 0;
}
a{
  text-decoration: none;
  color: black;
}
.xin p{
    color: red;
    font-size: 1rem;
    position: fixed;
    right: 0;
    bottom: 0;
    margin: 0 .5rem .5rem 0;
}
</style>


